@import './menu.scss';
@import './footer.scss';

html, body, div, p, img, h1, h2, h3, h4, h5 {
  box-sizing: border-box;
}

body {
	margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: sans-serif;
}

h1 {
  font-size: 2em;
}

.container {
  width: 980px;
  max-width: 100%;
  margin: 0 auto;
  padding: 0.5em;
}

.main-image {
  background: url("../images/matsumotoai.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  position: relative;
  width: 100%;
  height: 600px;
  border-radius: 2em;
  margin: 1em 0;
}

$icon-size: 64px;
.icon {
  width: $icon-size;
  height: $icon-size;
  border-radius: $icon-size/2;
  padding: 4px;
  position: absolute;
  top: 1em;
  left: 1em;
}


.content {
	font-size: 100%;

	& &--title {
		font-size: 120%;
	}

	  box-sizing: border-box;
  display: grid;
  grid-gap: 1rem;
  grid-template: "header  header"  1fr
                 "nav     main  "  minmax(100px, 2fr)
                 "nav     footer"  1fr /
                  300px   1fr;

  header { grid-area: header; }
  nav    { grid-area: nav   ; }
  main   { grid-area: main  ; }
  footer { grid-area: footer; }

}
